'use client';

import { useState, useEffect } from 'react';
import Sidebar from './Sidebar';
import MainArea from './MainArea';
import styles from './Dashboard.module.css';

export default function Dashboard() {
  const [tasks, setTasks] = useState([]);
  const [flares, setFlares] = useState([]);
  const [sanctuary, setSanctuary] = useState(false);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Fetch tasks from API
  useEffect(() => {
    const fetchTasks = async () => {
      try {
        setLoading(true);
        const res = await fetch('/api/tasks');
        if (!res.ok) throw new Error('Failed to fetch tasks');
        const data = await res.json();
        setTasks(data.tasks || []);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchTasks();
  }, []);

  // Toggle Sanctuary Mode
  useEffect(() => {
    const contentLayer = document.querySelector('.content-layer');
    if (contentLayer) {
      if (sanctuary) {
        contentLayer.classList.add('sanctuary-active');
      } else {
        contentLayer.classList.remove('sanctuary-active');
      }
    }
  }, [sanctuary]);

  const handleFlare = async (flareData) => {
    try {
      const res = await fetch('/api/flare', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(flareData),
      });
      if (!res.ok) throw new Error('Failed to send flare');
      const newFlare = await res.json();
      setFlares([newFlare, ...flares]);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div className={styles.viewport}>
      {/* Left Sidebar - Priority HUD */}
      <Sidebar 
        tasks={tasks} 
        sanctuary={sanctuary} 
        onSanctuaryChange={setSanctuary}
        onFlare={handleFlare}
        recentFlares={flares}
      />

      {/* Main Content Area */}
      <MainArea 
        tasks={tasks}
        loading={loading}
        error={error}
      />
    </div>
  );
}
